<template>
    <div class="web">
        <div style="padding-bottom: 10px;padding-left: 10px;box-sizing: border-box">
            <input v-model="inputValue" type="text" style="width: 40%;height: 40px;margin-top: 10px;background-color: #73b07b;padding: 5px;color: #2c4b42;box-sizing: border-box">
            <button @click="search" style="height: 40px;width: 70px;margin-left: 5px;color: #3a2222;font-size: 17px;background-color: #73b07b;">搜索</button>
            <button @click="reset" style="height: 40px;width: 70px;margin-left: 5px;color: #3a2222;font-size: 17px;background-color: #73b07b;">重置</button>
        </div>
        <div  v-for="(item,index) in webData" :key="index" class="wrap">
            <h2>{{index+1}}.{{item.title}}</h2>
            <ul  v-for="(item1,index1) in item.children" :key="index1">
                <li>
                    <a :href="item1.href">{{item1.name}}</a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'WebModule',
        data() {
            return {
                inputValue:'',
                dataBack:[],
            }
        },
        props:{
          webData:{
              type:Array,
              required:true,
              default:[]
          }
        },
        mounted() {
            this.$emit('concatArr', this.webData)
            this.webData.forEach((line) => {
                this.dataBack.push(line)
            })
        },
        methods: {
            reset(){
                this.inputValue = ''
                this.webData.splice(0, this.webData.length)
                this.dataBack.forEach((line) => {
                    this.webData.push(line)
                })
            },
            search(){
                // 每次搜索前，恢复备份，防止第二次搜出来的东西是在第一次的结果里搜 去掉前后空格
                this.inputValue = this.inputValue.trim()
                this.webData.splice(0, this.webData.length)
                // 这种赋值写法是为了不让dataBack的变化影响到备份 如果装lodash的话就用 _.cloneDeep就可以
                this.dataBack.forEach((line) => {
                    let children = []
                    line.children.forEach((item,index)=>{ //循环赋值，若使用push记得一开始初始化，要不然多次点击会越来越多
                         children.push(item)   //或this.loadData[index]=item
                    })
                    let obj ={
                        title: line.title,
                        children: children,
                    }
                    this.webData.push(obj)
                })
                if (this.inputValue  !== '') {
                    for (let i = 0; i < this.webData.length; i++) {
                        for (let j = 0; j < this.webData[i].children.length; j++) {
                            if (this.webData[i].children[j].name.indexOf(this.inputValue) === -1) {
                                let index = j
                                this.webData[i].children.splice(index, 1)
                                j = j - 1
                            }
                        }
                    }
                }
                for (let i = 0; i < this.webData.length; i++) {
                    if (this.webData[i].children.length === 0) {
                        let index = i
                        this.webData.splice(index, 1)
                        i = i - 1
                    }
                }
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @import '../../assets/css/variable';

    .web {
        width: 100%;
        background-color: $background_color2;

        h2 {
            line-height: 50px;
            padding-left: 20px;
            background-color: $background_color3;
        }

        h3 {
            line-height: 30px;
            padding-left: 20px;
            background-color: $background_color3;
        }

        h4 {
            line-height: 20px;
            padding-left: 25px;
            background-color: $background_color3;

            &:first-of-type {
                margin-top: 3px;
            }
        }

        ul {
            margin-left: 20px;
            display: inline-block;

            li {
                display: inline-block;
                margin: 15px;

                a {
                    color: $font-color2;

                    &:hover {
                        color: $font-color1;
                    }
                }
            }
        }
    }
</style>
